import { Message, MessageBox } from 'element-ui';

export function statementChange(parameterData, fieldTableData) {
  const listKey = [];
  let checkFlag = false;
  if (parameterData !== null) {
    parameterData.forEach((key) => {
      if (
        !key.code &&
        !key.name &&
        !key.type &&
        !key.required &&
        !key.defaultValue
      ) {
        checkFlag = false;
      } else {
        if (key.required === 3 && !key.defaultValue) {
          checkFlag = true;
          Message.error('是否必传为固定值，默认值不能为空，请输入默认值');
        } else if ((!key.code && !key.name) || !key.code || !key.name) {
          checkFlag = true;
          Message.error('参数code，参数名称不能为空，请输入');
        } else if (!key.name && key.code) {
          checkFlag = true;
          MessageBox('是否要将参数code作为参数名称?', '提示', {
            confirmButtonText: '是',
            cancelButtonText: '否',
            type: 'warning'
          })
            .then(() => {
              key.name = key.code;
            })
            .catch(() => {
              Message({
                type: 'info',
                message: '已取消'
              });
            });
        } else if (
          key.required === '' ||
          key.required === null ||
          key.required === undefined
        ) {
          checkFlag = true;
          Message.error('是否必传为不能为空，请输入');
        } else if (
          key.type === '' ||
          key.type === null ||
          key.type === undefined
        ) {
          checkFlag = true;
          Message.error('参数类型不能为空，请输入');
        }
      }
      // return checkFlag;
    });
  }
  fieldTableData.forEach((item) => {
    if (!item.fieldName && item.fieldCode) {
      checkFlag = true;
      MessageBox('是否要将字段code作为字段名称?', '提示', {
        confirmButtonText: '是',
        cancelButtonText: '否',
        type: 'warning'
      })
        .then(() => {
          item.fieldName = item.fieldCode;
        })
        .catch(() => {
          Message({
            type: 'info',
            message: '已取消'
          });
        });
    } else if (
      (!item.fieldCode && !item.fieldName) ||
      !item.fieldCode ||
      !item.fieldName
    ) {
      checkFlag = true;
      Message.error('字段code,字段名称不能为空');
    } else if (
      item.fieldType === '' ||
      item.fieldType === null ||
      item.fieldType === undefined
    ) {
      checkFlag = true;
      Message.error('字段类型不能为空');
    } else {
      if (item.isPrimaryKey === 0) {
        listKey.push(item);
      }
    }
    // return checkFlag;
  });
  if (
    fieldTableData[0].fieldCode &&
    fieldTableData[0].fieldName &&
    fieldTableData[0].fieldType
  ) {
    if (listKey.length === 0) {
      checkFlag = true;
      Message.error('字段列表必须存在主键');
    }
  }
  return checkFlag;
}
