


























import { Vue, Component, Prop, Emit, Watch } from 'vue-property-decorator';
import AdvancedScreening from './advanced.vue';
@Component({
  components: { AdvancedScreening }
})
export default class EditTest extends Vue {
  spread: any = {
    visible: true,
    model: {},
    items: [],
    loading: false
  };
  testForm: any = '';
  @Prop(Array) paramsList: [];
  @Prop({ default: false }) dialogTestSwitch: boolean;
  @Emit('saveEmitSwitch')
  treeTest() {}
  @Emit('cancelEmitSwitch')
  cancelSwitch() {}
  @Watch('paramsList', { immediate: true, deep: true })
  getVisible(newVal: any) {
    // (newVal: any, oldVal: any
    // console.log("newVal", newVal);
    const kong: Array<any> = [];
    const dataModel: any = {};
    newVal.forEach((item: any) => {
      dataModel[item.code] = item.defaultValue;
      if (item.type === 3) {
        kong.push({
          key: item.code,
          label: item.name ? item.name : item.code,
          type: 'switch',
          required: item.required,
          rule: {
            required: true,
            message: '请选择' + item.name,
            trigger: 'blur'
          }
        });
      } else if (item.type === 2) {
        kong.push({
          key: item.code,
          label: item.name ? item.name : item.code,
          type: 'date',
          TimePickerOptions: '',
          required: item.required,
          rule: {
            required: true,
            message: '请选择' + item.name,
            trigger: 'change'
          },
          clearable: true,
          disabled: false
        });
      } else {
        kong.push({
          type: 'input',
          key: item.code,
          label: item.name ? item.name : item.code,
          required: item.required,
          rule: {
            required: true,
            message: '请输入' + item.name,
            trigger: 'change'
          },
          placeholder: '请输入',
          config: {
            props: {
              type: item.type === 1 ? 'number' : 'input'
            }
          }
        });
      }
    });
    this.spread.items = kong;
    this.spread.model = dataModel;
  }
  adsfrom(val) {
    this.testForm = val;
  }
  treeDataTest() {
    this.testForm.validate((valid: any) => {
      if (valid) {
        this.treeTest();
      } else {
        return false;
      }
    });
  }
}
