<template>
  <div>
    <el-form
      ref="adsfrom"
      v-loading="screenLoading"
      :label-width="labelWidth"
      :model="model"
      :rules="rules"
      size="medium"
      label-suffix="："
      class="formCss labelSuffix"
    >
      <div v-for="item in items" :key="item.key" class="box">
        <el-form-item :label="item.label" :prop="item.key">
          <el-date-picker
            v-if="item.type === 'date'"
            v-model="model[item.key]"
            :type="item.type"
            :picker-options="item.TimePickerOptions"
            :clearable="item.clearable"
            :disabled="item.disabled"
            format="yyyy 年 MM 月 dd 日"
            value-format="yyyy-MM-dd"
          />
          <el-switch
            v-else-if="item.type === 'switch'"
            v-model="model[item.key]"
          ></el-switch>
          <item-render
            v-else
            :type="item.type"
            :config="{
              placeholder: item.placeholder || '',
              ...item.config,
              model,
              key: item.key
            }"
          />
        </el-form-item>
      </div>
    </el-form>
  </div>
</template>

<script>
import ItemRender from '@components/renders/ItemRender';
export default {
  name: 'AdvancedScreening',
  components: { 'item-render': ItemRender },
  props: {
    spread: {
      type: Boolean
    },
    model: {
      type: Object,
      default: () => {}
    },
    items: {
      type: Array,
      default: () => []
    },
    labelWidth: {
      type: String,
      default: () => ''
    },
    screenLoading: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      rules: {},
      boxBtnStyle: ''
    };
  },
  created() {
    this.initRules();
  },
  watch: {
    items() {
      this.initRules();
    }
  },
  methods: {
    initRules() {
      const rules = {};
      this.items.forEach((item) => {
        if (item.rule && item.required === 0) {
          rules[item.key] = item.rule;
        }
      });
      this.rules = rules;
      this.$nextTick(() => {
        this.$emit('adsfrom', this.$refs.adsfrom);
      });
    }
  }
};
</script>
