














































































import { Vue, Component, Prop, Watch } from 'vue-property-decorator';
import { cloneDeep } from 'lodash';
import { arrayFind, uuid } from '@utils/util';
import { dataViewFields } from '@api/viewData';

@Component({})
export default class TreeTableList extends Vue {
  @Prop(Array) dataList: [];
  @Prop({ default: '1' }) isCompatible: string;
  @Prop(Boolean) hasTree: false;
  @Prop({ default: '0' }) pushData: string;
  @Prop(Boolean) testChange;
  @Prop(Array) editTree: [];
  @Watch('isCompatible', { immediate: true })
  WatchIsCompatible() {
    if (this.isCompatible === '0') {
      this.delButton = true;
    } else {
      this.delButton = false;
    }
  }
  dialogVisible = false;
  delButton = false;
  searchView = '';
  treeName = '';
  treeCode = '';
  parameterId = 0;
  treeId = 0;
  parameterCode: any = '';
  treeProps: any = {
    children: 'fields',
    label: 'fieldName'
  };
  dragNodeData: any;
  tempList: any = [];
  currentNode = '';
  dragTableData: Array<any> = [];
  treeTableData: Array<any> = [];
  dataViewsTree: Array<any> = [];
  tableList: Array<any> = [
    {
      id: 1,
      fieldCode: '',
      fieldName: '',
      fieldType: '',
      foreignFieldCode: '',
      foreignId: '',
      isPrimaryKey: 1,
      addButton: true,
      edits: [1, 1, 1, 1, 0, 0, 0]
    }
  ];
  defaultProps: any = {
    label: 'fieldCode',
    value: 'fieldCode'
  };
  treeTable() {
    const _this = this;
    return [
      {
        prop: '',
        type: 'index',
        align: 'center',
        label: '序号',
        width: '70'
      },
      {
        prop: 'fieldCode',
        label: '字段code',
        'header-align': 'left',
        'min-width': '100'
      },
      {
        prop: 'fieldName',
        label: '字段名称',
        'header-align': 'left',
        'min-width': '100',
        editer: {
          type: 'input',
          key: 'fieldName',
          placeholder: '请输入字段名称'
        }
      },
      {
        prop: 'fieldType',
        'header-align': 'left',
        label: '字段类型',
        'min-width': '120',
        editer: {
          type: 'select',
          key: 'fieldType',
          config: {
            props: {
              placeholder: '请选择字段类型'
            },
            options: () => {
              return [
                {
                  label: '字符串',
                  value: 0
                },
                {
                  label: '数字',
                  value: 1
                },
                {
                  label: '日期',
                  value: 2
                },
                {
                  label: '布尔',
                  value: 3
                }
              ];
            }
          }
        }
      },
      {
        prop: 'foreignFieldCode',
        label: '关联视图',
        align: 'center',
        'min-width': '100',
        render(h: any, { record }: any) {
          if (record.treeCode === true) {
            return h('span', [
              h('span', record.foreignFieldName),
              h('el-button', {
                props: {
                  type: 'text',
                  icon: 'el-icon-delete'
                },
                style: 'margin-left: 10px',
                on: {
                  click: () => _this.deleteFiled(record)
                }
              })
            ]);
          } else if (record.treeButton === true) {
            return h('span', record.foreignFieldName);
          } else {
            return h('span', [
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _this.setFiled(record)
                  }
                },
                '设置'
              )
            ]);
          }
        }
      },
      {
        prop: 'isPrimaryKey',
        label: '是否主键',
        'header-align': 'left',
        'min-width': '100',
        render(h: any, { $index, record }: any) {
          return h('el-switch', {
            props: {
              value: record.isPrimaryKey,
              'active-value': 0,
              'inactive-value': 1,
              'active-text': '是',
              'inactive-text': '否'
            },
            on: {
              change: () => _this.changKey(record, $index)
            }
          });
        }
      },
      {
        prop: '',
        align: 'center',
        label: '操作',
        width: '100',
        headerAlign: 'center',
        render(h: any, { $index }: any) {
          return h('span', [
            h(
              'el-button',
              {
                props: {
                  type: 'text',
                  disabled: _this.delButton
                },
                on: {
                  click: () => _this.handleDelete($index)
                }
              },
              '删除'
            )
          ]);
        }
      }
    ];
  }
  get treeListTable() {
    this.dragTableData = this.dataList;
    const columns = this.treeTable();
    const fieldCodeType: any = [];
    if (this.dataList.length) {
      this.dataList.forEach((item: any) => {
        if (!item.fieldCode) {
          fieldCodeType.push(item);
        }
      });
    }
    if (this.dataList.length === 0 || fieldCodeType.length > 0) {
      columns[1].editer = {
        type: 'input',
        key: 'fieldCode',
        placeholder: '请输入字段code'
      };
      columns[6].render = (h, { record }) => {
        if (record.addButton === true) {
          return h('span', [
            h(
              'el-button',
              {
                props: {
                  type: 'text'
                },
                on: {
                  click: () => this.deleteEdit(record)
                }
              },
              '删除'
            ),
            h(
              'span',
              {
                style: 'padding: 0 5px'
              },
              '|'
            ),
            h(
              'el-button',
              {
                props: {
                  type: 'text'
                },
                on: {
                  click: () => this.addEdit(record)
                }
              },
              '添加'
            )
          ]);
        } else {
          return h('span', [
            h(
              'el-button',
              {
                props: {
                  type: 'text'
                },
                on: {
                  click: () => this.deleteEdit(record)
                }
              },
              '删除'
            )
          ]);
        }
      };
    }
    return columns;
  }
  get treeDataList(): any {
    let list: Array<any> = this.tableList;
    if (this.dataList.length > 0) {
      if (this.hasTree) {
        if (this.editTree && this.editTree.length > 0) {
          if (this.testChange === true) {
            this.treeTableData = this.tableList;
          } else {
            this.treeTableData = this.editTree;
          }
        }
        // list = cloneDeep(this.treeTableData);
        list = this.treeTableData;
      } else {
        list = this.dataList;
      }
      list.forEach((item) => {
        if (!Object.prototype.hasOwnProperty.call(item, 'isPrimaryKey')) {
          item.isPrimaryKey = 1;
        }
        if (!item.rule) {
          item.rule = [];
        }
        item.edits = [0, 1, 1, 1, 0, 0, 0];
      });
    }
    return list;
  }
  allowDrop(draggingNode: any, dropNode: any, type: string) {
    if (draggingNode.level === dropNode.level) {
      if (draggingNode.parent.id === dropNode.parent.id) {
        return type === 'prev';
      }
    } else {
      // 不同级进行处理
      return false;
    }
  }
  changKey(record, index: any) {
    this.treeDataList.splice(index, 1, {
      ...record,
      isPrimaryKey: 1 - record.isPrimaryKey
    });
  }
  dataDragStart(startNode: object) {
    this.dragNodeData = startNode;
  }
  handleSomethingDrop() {
    if (this.dragNodeData.data) {
      this.handleData(this.dragNodeData);
    }
  }
  handleData(dragNodeData: any) {
    // 遍历子节点
    if (dragNodeData.childNodes && dragNodeData.childNodes.length > 0) {
      dragNodeData.childNodes.forEach((item: any) => {
        this.handleData(item);
      });
    } else {
      // 组装当前拖动节点的层级结构
      const newNode = cloneDeep(dragNodeData.data);
      newNode.rule = this.getNodeParents(dragNodeData);

      const exsitNode = arrayFind(
        this.treeTableData,
        'fieldCode',
        newNode.fieldCode
      );
      if (exsitNode) {
        if (
          exsitNode.rule.length === newNode.rule.length &&
          exsitNode.rule.every(
            (cell, index) => cell.node === newNode.rule[index]['node']
          )
        ) {
          this.$message({
            type: 'warning',
            message: '字段重复'
          });
          return;
        } else {
          if (newNode.rule.length > 1) {
            newNode.fieldCode = newNode.rule.map((cell) => cell.node).join('');
          } else {
            newNode.fieldCode += uuid().substr(0, 3);
          }
        }
      }
      this.treeTableData.push(newNode);
      this.treeTableData.forEach((item, index) => {
        if (!item.fieldCode) {
          this.treeTableData.splice(index, 1);
        }
      });
    }
  }
  // 查找一个节点的所有父节点
  getNodeParents(node) {
    const parents: Array<any> = [];
    const data = node.data;
    if (data) {
      if (data.fieldCode && data.fieldCode !== 'items') {
        parents.unshift({
          node: data.fieldCode,
          type: data.type === 'array' ? '1' : '0'
        });
      }
    }
    // 查询父节点
    if (node.parent) {
      parents.unshift(...this.getNodeParents(node.parent));
    }
    return parents;
  }

  handleDelete(index: number) {
    //, row: object
    this.$confirm('是否删除这条记录?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning',
      center: true
    })
      .then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
        this.treeDataList.splice(index, 1);
      })
      .catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
  }
  // 添加
  addEdit(row: any) {
    const addData: Array<any> = Object.assign({}, row);
    for (const i in addData) {
      if (i === 'id') {
        addData[i] = addData[i] + 1;
      } else if (i !== 'edits') {
        addData[i] = '';
      }
    }
    this.treeDataList.push(addData);
    if (this.treeDataList.length > 1) {
      this.treeDataList.forEach((item: any) => {
        this.$delete(item, 'addButton');
      });
    }
    const addView: Array<any> = this.treeDataList.slice(-1);
    addView.forEach((item: any) => {
      this.$set(item, 'addButton', true);
    });
  }
  // 删除
  deleteEdit(row: any) {
    if (this.treeDataList.length > 1) {
      this.$confirm('是否删除这条记录?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        center: true
      })
        .then(() => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
          for (let i = 0; i < this.treeDataList.length; i++) {
            if (this.treeDataList[i].id === row.id) {
              this.treeDataList.splice(i, 1);
            }
          }
          const addView = this.treeDataList.slice(-1);
          addView.forEach((item: any) => {
            this.$set(item, 'addButton', true);
          });
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
    }
  }
  // 设置
  setFiled(record: any) {
    this.parameterId = record.id;
    this.parameterCode = record.fieldCode;
    this.dialogVisible = true;
  }
  // 设置弹框树选择
  handleNodeClick(node: any) {
    this.treeId = node.id;
    this.treeName = node.fieldName;
    this.treeCode = node.fieldCode;
  }
  setDetermines() {
    if (this.treeId && this.treeName) {
      this.treeDataList.forEach((item: any) => {
        if (item.id) {
          if (item.id === this.parameterId) {
            item.foreignId = this.treeId;
            item.foreignFieldCode = this.treeCode;
            item.foreignFieldName = this.treeName;
            this.$set(item, 'treeButton', true);
          }
        } else {
          if (item.fieldCode === this.parameterCode) {
            item.foreignId = this.treeId;
            item.foreignFieldCode = this.treeCode;
            item.foreignFieldName = this.treeName;
            this.$set(item, 'treeButton', true);
          }
        }
      });
      this.dialogVisible = false;
    } else {
      this.$message.error('请选择关联字段');
    }
  }
  cancelDialog() {
    this.dialogVisible = false;
  }
  deleteFiled(row: any) {
    this.$confirm('确定要删除该关联字段吗?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning',
      center: true
    })
      .then(() => {
        this.$delete(row, 'treeButton');
        this.$delete(row, 'treeCode');
        row.foreignFieldCode = '';
        row.foreignFieldName = '';
        row.foreignName = '';
        row.foreignId = '';
      })
      .catch(() => {});
  }
  cellMouseEnter(row: any, column: any) {
    if (column.property === 'foreignFieldCode' && row.foreignFieldCode) {
      this.$delete(row, 'treeButton');
      this.$set(row, 'treeCode', true);
    }
  }
  cellMouseleave(row: any) {
    if (row.foreignFieldCode) {
      this.$delete(row, 'treeCode');
      this.$set(row, 'treeButton', true);
    }
  }
  queryDataViewsFields() {
    dataViewFields({ dictFlag: '0' }).then(({ data }) => {
      if (data) {
        data.forEach((item: any) => {
          item.fieldName = item.name;
        });
        this.dataViewsTree = data;
      }
    });
  }
  // 关联视图模糊查询
  changeSearch() {
    const params = {
      dictFlag: '0',
      type: '0',
      searchName: this.searchView
    };
    dataViewFields(params).then(({ data }) => {
      if (data) {
        data.forEach((item: any) => {
          item.fieldName = item.name;
        });
        this.dataViewsTree = data;
      }
    });
  }

  created() {
    this.queryDataViewsFields();
  }
}
