

















import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import { arrayFind } from '@utils/util';
@Component({
  components: {}
})
export default class ParameView extends Vue {
  @Prop({ default: 1 }) dictFlag: number;
  @Prop({ default: 1 }) isCompatible: any;
  @Prop({ default: [] }) actions: Array<any>;
  @Prop(String) parameterData: Array<any>;
  @Prop(Boolean) httpService: false;
  @Watch('parameterData', { immediate: true })
  WatchparameterData() {
    this.changeDis();
    this.changeButton();
  }
  @Watch('dictFlag')
  WatchDictFlag() {
    if (this.dictFlag === 0) {
      this.requiredlist = [
        {
          label: '固定值',
          value: 3
        }
      ];
    } else {
      this.requiredChange();
      this.parameterData.forEach((item) => {
        item.required = '';
      });
    }
  }
  private typeList: Array<any> = [
    {
      label: '字符串',
      value: 0
    },
    {
      label: '数字',
      value: 1
    },
    {
      label: '日期',
      value: 2
    },
    {
      label: '布尔',
      value: 3
    }
  ];
  dialogVisible = false;
  changeDisabled = false;
  changeDisType = false;
  isAdd = '';
  requiredlist: Array<any> = [];
  columns() {
    const _this = this;
    return [
      {
        prop: '',
        type: 'index',
        align: 'center',
        label: '序号',
        width: '70'
      },
      {
        prop: 'code',
        label: '参数code',
        'header-align': 'left',
        'min-width': '100',
        // editer: {
        //   type: 'input',
        //   key: 'code',
        //   config: {
        //     props: {
        //       placeholder: '请输入参数code',
        //       disabled: _this.changeDisabled
        //     }
        //   }
        // }
        render(h, { record }) {
          if (record.isEdit === true) {
            return h('el-input', {
              props: {
                value: record.code,
                clearable: true,
                placeholder: '请输入参数code'
              },
              on: {
                input: (value) => {
                  record.code = value;
                }
              }
            });
          } else {
            return h('span', record.code);
          }
        }
      },
      {
        prop: 'name',
        'header-align': 'left',
        label: '参数名称',
        'min-width': '120',
        editer: {
          type: 'input',
          key: 'name',
          clearable: true,
          placeholder: '请输入参数名称'
        }
      },
      {
        prop: 'type',
        'header-align': 'left',
        label: '参数类型',
        'min-width': '120',
        render(h, { record }) {
          if (record.isEdit === false) {
            const typeItem = arrayFind(_this.typeList, 'value', record.type);
            const nameType = typeItem ? typeItem.label : record.type;
            return h('span', nameType);
          } else {
            const option = _this.typeList.map((item) => {
              return h('el-option', {
                props: {
                  value: item.value,
                  key: item.value,
                  label: item.label
                }
              });
            });
            return h(
              'el-select',
              {
                props: {
                  value: record.type,
                  clearable: true,
                  placeholder: '请输入参数类型'
                },
                on: {
                  change: (val) => {
                    record.type = val;
                  }
                }
              },
              option
            );
          }
        }
        // editer: {
        //   type: 'select',
        //   key: 'type',
        //   config: {
        //     props: {
        //       placeholder: '请选择参数类型',
        //       disabled: _this.changeDisabled
        //     },
        //     options: () => _this.typeList
        //   }
        // }
      },
      {
        prop: 'required',
        'header-align': 'left',
        label: '是否必传',
        'min-width': '120',
        editer: {
          type: 'select',
          key: 'required',
          config: {
            props: {
              placeholder: '请选择是否必传',
              clearable: true
            },
            options: () => _this.requiredlist
          }
        }
      },
      {
        prop: 'defaultValue',
        'header-align': 'left',
        label: '默认值',
        'min-width': '120',
        editer: {
          type: 'input',
          key: 'defaultValue',
          placeholder: '请输入默认值'
        }
      },
      {
        prop: '',
        align: 'center',
        label: '操作',
        width: '100',
        headerAlign: 'center',
        render(h: any, { record }: any) {
          if (record.addButton === true) {
            return h('span', [
              h(
                'el-button',
                {
                  props: {
                    type: 'text',
                    disabled: record.delButton
                  },
                  on: {
                    click: () => _this.deleteEdit(record)
                  }
                },
                '删除'
              ),
              h(
                'span',
                {
                  style: 'padding: 0 5px'
                },
                '|'
              ),
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _this.addEdit(record)
                  }
                },
                '添加'
              )
            ]);
          } else {
            return h('span', [
              h(
                'el-button',
                {
                  props: {
                    type: 'text',
                    disabled: record.delButton
                  },
                  on: {
                    click: () => _this.deleteEdit(record)
                  }
                },
                '删除'
              )
            ]);
          }
        }
      }
    ];
  }
  columnsHttp() {
    const _this = this;
    return [
      {
        prop: '',
        type: 'index',
        align: 'center',
        label: '序号',
        width: '70'
      },
      {
        prop: 'code',
        label: '参数code',
        'header-align': 'left',
        'min-width': '100',
        editer: {
          type: 'input',
          key: 'code',
          placeholder: '请输入参数code'
        }
      },
      {
        prop: 'name',
        'header-align': 'left',
        label: '参数名称',
        'min-width': '120',
        editer: {
          type: 'input',
          key: 'name',
          placeholder: '请输入参数名称'
        }
      },
      {
        prop: 'transType',
        'header-align': 'left',
        label: '传输方式',
        'min-width': '120',
        editer: {
          type: 'select',
          key: 'transType',
          config: {
            props: {
              placeholder: '请选择传输方式'
            },
            options: () => {
              return [
                {
                  label: '路径参数',
                  value: 0
                },
                {
                  label: 'formdata',
                  value: 2
                }
              ];
            }
          }
        }
      },
      {
        prop: 'type',
        'header-align': 'left',
        label: '参数类型',
        'min-width': '120',
        editer: {
          type: 'select',
          key: 'type',
          config: {
            props: {
              placeholder: '请选择参数类型'
            },
            options: () => {
              return [
                {
                  label: '字符串',
                  value: 0
                },
                {
                  label: '数字',
                  value: 1
                },
                {
                  label: '日期',
                  value: 2
                },
                {
                  label: '布尔',
                  value: 3
                }
              ];
            }
          }
        }
      },
      {
        prop: 'required',
        'header-align': 'left',
        label: '是否必传',
        'min-width': '120',
        editer: {
          type: 'select',
          key: 'required',
          config: {
            props: {
              placeholder: '请选择是否必传'
            },
            options: () => _this.requiredlist
          }
        }
      },
      {
        prop: 'defaultValue',
        'header-align': 'left',
        label: '默认值',
        'min-width': '120',
        editer: {
          type: 'input',
          key: 'defaultValue',
          placeholder: '请输入默认值'
        }
      },
      {
        prop: '',
        align: 'center',
        label: '操作',
        width: '100',
        headerAlign: 'center',
        render(h: any, { record }: any) {
          if (record.addButton === true) {
            return h('span', [
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _this.deleteEdit(record)
                  }
                },
                '删除'
              ),
              h(
                'span',
                {
                  style: 'padding: 0 5px'
                },
                '|'
              ),
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _this.addEdit(record)
                  }
                },
                '添加'
              )
            ]);
          } else {
            return h('span', [
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _this.deleteEdit(record)
                  }
                },
                '删除'
              )
            ]);
          }
        }
      }
    ];
  }
  // 添加
  addEdit(row) {
    const addData = Object.assign({}, row);
    for (const i in addData) {
      if (i === 'frontId') {
        addData[i] = addData[i] + 1;
      } else if (i !== 'edits') {
        addData[i] = '';
      }
    }
    addData.isCompatible = '1';
    addData.delButton = false;
    addData.isEdit = true;
    this.parameterData.push(addData);
    if (this.parameterData.length > 1) {
      this.parameterData.forEach((item) => {
        this.$delete(item, 'addButton');
      });
    }
    this.changeButton();
  }
  changeButton() {
    const addView = this.parameterData.slice(-1);
    addView.forEach((item) => {
      this.$set(item, 'addButton', true);
    });
  }
  disabledchange() {
    if (this.isCompatible) {
      this.parameterData.forEach((item) => {
        this.$set(item, 'disabled', true);
        if (item.disabled === true) {
          return true;
        } else {
          return false;
        }
      });
    }
  }
  // 删除
  deleteEdit(row: any) {
    if (this.parameterData.length > 1) {
      for (let i = 0; i < this.parameterData.length; i++) {
        if (this.parameterData[i].frontId === row.frontId) {
          this.parameterData.splice(i, 1);
        }
      }
      const addView = this.parameterData.slice(-1);
      addView.forEach((item) => {
        this.$set(item, 'addButton', true);
      });
    }
  }
  // 兼容非兼容修改判断
  requiredChange() {
    if (this.isCompatible === '0') {
      this.requiredlist = [
        {
          label: '否',
          value: 1
        },
        {
          label: '固定值',
          value: 3
        }
      ];
    } else {
      this.requiredlist = [
        {
          label: '是',
          value: 0
        },
        {
          label: '否',
          value: 1
        },
        {
          label: '固定值',
          value: 3
        }
      ];
    }
  }
  changeDis() {
    if (this.isCompatible === '0' || this.isCompatible === '1') {
      this.isAdd = '0';
    }
    if (this.isAdd !== '0') {
      this.parameterData.forEach((item) => {
        item.isEdit = true;
        item.delButton = false;
        item.edits = [0, 1, 1, 1, 1, 1, 1, 0];
      });
    } else {
      if (this.parameterData.length > 0) {
        this.parameterData.forEach((item) => {
          if (item.isCompatible === '1') {
            item.delButton = false;
            item.isEdit = true;
          } else {
            item.delButton = true;
            item.isEdit = false;
          }
          if (this.isCompatible === '1') {
            item.delButton = false;
          }
          item.edits = [0, 1, 1, 1, 1, 1, 1, 0];
        });
      } else {
        this.parameterData = [
          {
            frontId: 1,
            code: '',
            name: '',
            type: '',
            required: '',
            defaultValue: '',
            isEdit: true,
            isCompatible: '1',
            addButton: true,
            edits: [0, 1, 1, 1, 1, 1, 0]
          }
        ];
      }
    }
  }
  // 初始化
  mounted() {
    this.requiredChange();
  }
}
